@import "../../styles/variables.sass";

// NOTE: If you need to style the content of notifications, put the rules under
// .notification-container in Farmhand.sass.
.NotificationSystem {
  .wrapper {
    max-width: $break-sm; }

  .notification {
    align-items: stretch;
    display: flex;
    flex-direction: column-reverse;
    margin: 0 4.5em 0;
    top: 4.75em;

    @media (min-width: #{$break-sm}) {
      top: 5em; }

    .MuiAlert-root {
      margin-bottom: 1em; } } }

